import {UserActionTypes} from "./user.types";

const INITIAL_STATE = {
    user_name: null,
}

const userReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case UserActionTypes.SET_USER_NAME:
            return {
                ...state,
                user_name: action.payload
            };
        default:
            return state;
    }
}
export default userReducer;